@import url(https://fonts.googleapis.com/css?family=Orbitron|Josefin+Sans&display=swap);
body {
  margin: 0;
  font-family: -apple-system, BlinkMacSystemFont, "Segoe UI", "Roboto", "Oxygen",
    "Ubuntu", "Cantarell", "Fira Sans", "Droid Sans", "Helvetica Neue",
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}
/*
	# fonts
	font-family: 'Orbitron', sans-serif; - time display digits
	font-family: 'Josefin Sans', sans-serif; - else
	
	# colours
	background-dark : #212121
	basic : #4287f5
	basic-dark : #182f54
	basic-light : #91d5ff
*/

.App {
	width : 100vw;
	min-height : 560px;
	overflow-y : scroll;
	height : 100vh;
	display : grid;
	grid-template-areas : 
		'left-side-container . right-side-container'
		'timer-container timer-container timer-container'
		'first-bottom-container first-bottom-container first-bottom-container'
		'second-bottom-container second-bottom-container second-bottom-container'
		'footer footer footer'
	;
	justify-content : center;
	justify-items : center;
	background-color : #212121;
	font-family : 'Josefin Sans', sans-serif;
	align-items : center;
	align-content : center;
	grid-column-gap : 20px;
}

.timer-container {
	grid-area : timer-container;
	display : flex;
	justify-content : center;
	align-items : center;
	background-color : inherit;
	margin : 15.5vh 0px 12.5vh 0px;
}

@media only screen and (min-width : 380px) {
	.timer-container {
		margin : 19vh 0px 16vh 0px;
	}
}

.container-title {
	color : #4287f5;
	font-size : 1.2em;
	wrap-overflow : word;
	text-align : center;
	width : 70px;
}

.control-container {
	border : 3px solid #182f54;
	border-radius : 10px;
	width : 80px;
}

.content {
	display : grid;
	grid-template-columns : auto auto auto;
	justify-content : center;
	justify-items : center;
	grid-column-gap : 10px;
}

@media only screen and (min-width : 768px) {
	.App {
		grid-template-areas : 
			'left-side-container timer-container right-side-container'
			'first-bottom-container first-bottom-container first-bottom-container'
			'second-bottom-container second-bottom-container second-bottom-container'
			'. footer .'
		;
		grid-gap : 20px 45px;
	}
	
	.timer-container {
		margin : 20vh 70px 14vh 60px;
	}
	
	.content {
		grid-template-columns : auto;
		justify-content : center;
		justify-items : center;
		grid-row-gap : 5px;
	}
	
	.container-title {
		font-size : 1.8em;
		width : 110px;
	}
	
	.control-container {
		width : 120px;
	}
}

.control-number {
	color : #4287f5;
	font-family : 'Orbitron', sans-serif;
	font-size : 1.5em;
	min-width : 35px;
	min-height : 30px;
	text-align : center;
}

.control-operator {
	color : #4287f5;
	font-size : 1.5em;
}

@media only screen and (min-width : 768px) {
	.control-number {
		font-size : 1.8em;
	}
	
	.control-operator {
		font-size : 1.8em;
	}
}

.control-container[side~=left] {
	grid-area : left-side-container;
}

.control-container[side~=right] {
	grid-area : right-side-container;
}

.control-container[side~=bottom-first] {
	grid-area : first-bottom-container;
	width : 160px;
}

.control-container[side~=bottom-first] .content {
	display : grid;
	grid-template-columns : auto auto auto;
	align-content : center;
	align-items : center;
	grid-column-gap : 10px;
}

.control-container[side~=bottom-first] .container-title {
	display : inline-block;
	width : 125px;
}

#breakLengthFlag {
}

.control-container[side~=bottom-second] {
	grid-area : second-bottom-container;
	display : grid;
	grid-template-columns : auto auto;
	grid-template-rows : auto;
	justify-content : center;
	grid-column-gap : 20px;
	margin-top : 10px;
	margin-bottom : 20px;
	border : none;
}

.footer {
	grid-area : footer;
}

.author {
	text-align : center;
	text-decoration : none;
	color : #4287f5;
	opacity : 0.8;
	font-size : 0.9em;
}

@media only screen and (min-width : 768px) {
	.author {
		font-size : 1.2em;
	}
}

.author:hover {
	cursor : pointer;
}

.author:active {
	opacity : 1;
}

.display {
	font-family : 'Orbitron', sans-serif;
	display : flex;
	flex-direction : column;
	justify-content : center;
	align-content : center;
	text-align : center;
}

.sessionTime {
	font-size : 2.4em;
	color : #4287f5;
}

.breakTime {
	font-size : 1.6em;
	color : #91d5ff;
}

@media only screen and (min-width : 768px) {
	.sessionTime {
		font-size : 3.6em;
	}
	
	.breakTime {
		font-size : 2.8em;
	}
}

.button {
	z-index : 20;
	color : #4287f5;
	-webkit-transition-duration : 0.2s;
	        transition-duration : 0.2s;
	border : none;
	background-color : inherit;
	outline : none;
}

.button:active {
	-webkit-transition-duration : 0.2s;
	        transition-duration : 0.2s;
	-webkit-transform : scale(0.9);
	        transform : scale(0.9);
	color : #91d5ff;
}

.button:hover {
	cursor : pointer;
}

.button-control {
	font-size : 3.2em;
}

@media screen only and (min-width : 768px) {
	.button:hover {
		cursor : pointer;
		color : #91d5ff;
		-webkit-transition-duration : 0.2s;
		        transition-duration : 0.2s;
	}
}



.CircularProgressBar {
	position:absolute;
	display : flex;
	justify-content : center;
	align-items : center;
	background-color : inherit;
}

.CircularProgressBar[id~=breakTimerprogressBar] {
	-webkit-transform : scale(0.8);
	        transform : scale(0.8);
}
.CircularProgressBar[id~=sessionTimerprogressBar] {
	-webkit-transform : scale(0.65);
	        transform : scale(0.65);
}

@media only screen and (min-width : 380px) {
	.CircularProgressBar[id~=breakTimerprogressBar] {
		-webkit-transform : scale(0.9);
		        transform : scale(0.9);
	}
	.CircularProgressBar[id~=sessionTimerprogressBar] {
		-webkit-transform : scale(0.7);
		        transform : scale(0.7);
	}
}

@media only screen and (min-width : 400px) {
	.CircularProgressBar[id~=breakTimerprogressBar] {
		-webkit-transform : scale(0.95);
		        transform : scale(0.95);
	}
	.CircularProgressBar[id~=sessionTimerprogressBar] {
		-webkit-transform : scale(0.75);
		        transform : scale(0.75);
	}
}

@media only screen and (min-width : 420px) {
	.CircularProgressBar[id~=breakTimerprogressBar] {
		-webkit-transform : scale(1);
		        transform : scale(1);
	}
	.CircularProgressBar[id~=sessionTimerprogressBar] {
		-webkit-transform : scale(0.8);
		        transform : scale(0.8);
	}
}

@media only screen and (min-width : 768px) {
	.CircularProgressBar[id~=breakTimerprogressBar] {
		-webkit-transform : scale(1.2);
		        transform : scale(1.2);
	}
	.CircularProgressBar[id~=sessionTimerprogressBar] {
		-webkit-transform : scale(1);
		        transform : scale(1);
	}
}

.wrapper {
	position : absolute;
	width : 300px;
	height : 300px;
	-webkit-animation-name : wrapper-show;
	        animation-name : wrapper-show;
	-webkit-animation-fill-mode : forwards;
	        animation-fill-mode : forwards;
	-webkit-animation-timing-function : linear;
	        animation-timing-function : linear;
	-webkit-animation-duration : 0.1s;
	        animation-duration : 0.1s;
}

/* Set the progress bar */
.circle {
	width: 270px;
	height : 270px;
	border : 14px solid #4287f5;
	border-radius: 50%;
	position: absolute;
	clip : rect(0px,149px,298px,0px);
	z-index : 10;
	-webkit-animation-timing-function : linear;
	        animation-timing-function : linear;	
	-webkit-animation-fill-mode : forwards;	
	        animation-fill-mode : forwards;
}

.circle-background {
	width: 260px;
	height : 260px;
	border : 25px solid #182f54;
	border-radius: 50%;
	position: absolute;
	z-index : 5;
}

.start-gap {
	position : absolute;
	top  : -156px;
	width : 25px;
	height : 27px;
	background-color : inherit;
	z-index : 15;
}

.start-gap[part~=left] {
	-webkit-transform : skewX(20deg);
	        transform : skewX(20deg);
}

.start-gap[part~=right] {
	-webkit-transform : skewX(-20deg);
	        transform : skewX(-20deg);
}

.right-spin {
	-webkit-transform : rotate(180deg);
			transform : rotate(180deg);
	-webkit-animation-name : right-spin;
	        animation-name : right-spin;
}

.left-spin {
	-webkit-animation-name : left-spin;
	        animation-name : left-spin;
}

@-webkit-keyframes right-spin {
	from {
		-webkit-transform : rotate(180deg);
		        transform : rotate(180deg);
	}
	to {
		-webkit-transform : rotate(0deg);
		        transform : rotate(0deg);
	}
}

@keyframes right-spin {
	from {
		-webkit-transform : rotate(180deg);
		        transform : rotate(180deg);
	}
	to {
		-webkit-transform : rotate(0deg);
		        transform : rotate(0deg);
	}
}

@-webkit-keyframes left-spin {
	from {
		-webkit-transform : rotate(360deg);
		        transform : rotate(360deg);
	}
	to {
		-webkit-transform : rotate(0deg);
		        transform : rotate(0deg);
	}
}

@keyframes left-spin {
	from {
		-webkit-transform : rotate(360deg);
		        transform : rotate(360deg);
	}
	to {
		-webkit-transform : rotate(0deg);
		        transform : rotate(0deg);
	}
}

@-webkit-keyframes wrapper-show {
	to {
		clip : rect(0px,300px,300px,149px);
	}
}

@keyframes wrapper-show {
	to {
		clip : rect(0px,300px,300px,149px);
	}
}
